.TodoItem {
  background-color: white;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);

}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color:#454545;
  font-weight: bolder;
}
svg.TodoCheck.IconCheck-active {
  color:#4EC3E0;
}
.TodoItem-p--complete {
  text-decoration: line-through;
  color:#8ce829;
}


.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;

}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #132E37;
}

.Icon-delete {
  top: -24px;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}